.card {
    background-color: #fff;
    border-radius: 5px;
    display: flex;
    flex-direction: column;
    align-items: center;
    margin: 0.5rem;
    padding: 1rem;
    max-width: 300px;
    min-height: 590px;
    box-shadow: 0 1px 1.5px 0 rgba(0, 0, 0, 0.2);
}
.card-image {
    width: 200px;
    height: 200px;
}
.card-content {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    width: 95%;
}
.card-name {
    width: 80%;
    font-size: 1.2rem;
    color: #212121;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
}
.card-description {
    font-size: 0.9rem;
    color: #212121;
    text-align: center;
}
.card-price {
    font-size: 1.5rem;
    color: #212121;
    font-weight: bold;
}
.card-stock {
    font-size: 1rem;
    color: #212121;
}
.card-button-container {
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 1rem;
}
.card-button-minus {
    background-color: #fff;
    border: 1px solid #212121;
    border-radius: 50%;
    width: 2rem;
    height: 2rem;
    justify-content: center;
    align-items: center;
    cursor: pointer;
}
input {
    text-align: center;
}
.card-input {
    background-color: #fff;
    border: 1px solid #212121;
    width: 2rem;
    height: 2rem;
    font-size: 1rem;
    margin:  0 0.5rem;
}
.card-button-plus {
    background-color: #fff;
    border: 1px solid #212121;
    border-radius: 50%;
    width: 2rem;
    height: 2rem;
    justify-content: center;
    align-items: center;
    cursor: pointer;
}